<template>
  <div class="card" style="height: 95%;">
    <div class="card__title">
      <b-row>
        <b-col style="max-width:max-content !important;">
          <span>Fleet Master</span>
        </b-col>
        <b-col style="text-align: right;">
          <span>
            <ABSButton
              :text="'View All'"
              classButton="button button--new"
              classIcon="icon-style-1"
              :disabled="false"
              @click="doViewAllFleetMaster"
            />
          </span>
        </b-col>
      </b-row>
    </div>
    <div class="card__body">
        <ACCFormList
            :prop="propList"
            @rowClicked="rowClicked"
            ref="ref_OPFleetMaster"
        >
            <template slot="kir_expire" slot-scope="data">
              <span v-if="data.item.kir_expiry_status == 'Y'" style="color: red">{{data.item.kir_expire}}</span>
              <span v-else>{{data.item.kir_expire}}</span>
            </template>
            <template slot="plate_expire" slot-scope="data">
              <span v-if="data.item.license_plate_expiry_status == 'Y'" style="color: red">{{data.item.plate_expire}}</span>
              <span v-else>{{data.item.plate_expire}}</span>
            </template>
            <template slot="stnk_expire" slot-scope="data">
              <span v-if="data.item.stnk_expiry_status == 'Y'" style="color: red">{{data.item.stnk_expire}}</span>
              <span v-else>{{data.item.stnk_expire}}</span>
            </template>
        </ACCFormList>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
        propList: {
            OptionUrl: "/SP/SP_Dashboard",
            initialWhere: "ss_portfolio_id='" + this.getDataUser().portfolio_id + "'",
            LineNo: 1,
            PageLevel: 1,
            TabIndex: 1,
            OrderBy: "",
            SourceField: "",
            ParamView: "",
            PerPage: 8,
        }
    };
  },
  methods: {
    rowClicked(record, index) {
      var param = record;
      this.$store.commit("setParamPage", param);
      this.$router.push({ name: "OP_FleetMasterView" });
    },
    doViewAllFleetMaster() {
      this.$router.push({ name: "OP_FleetMaster" });
    }
  },
  mounted() {
    this.$refs.ref_OPFleetMaster.doGetList("");
  },
};
</script>